<template>
	<view class="delivery-order-detail">
		<template v-if="order">
			<view class="track-map">
				<map :latitude="order.lat" :longitude="order.lng" :scale="10"></map>
			</view>
			
			<view class="track-text-box">
				
				<order-header class="order-header-out" :order="order">
					<view class="stub-button" @click="onClickFiles">
						<text>电子存根</text>
					</view>
				</order-header>
				
				<order-delivery-info class="order-delivery-info-out" :order="order"></order-delivery-info>
				
				<view class="track-list">
					<scroll-view scroll-y>
						<timeline>
							<timeline-item
								v-for="(item, index) in timelineList"
								:key="index"
								:time="item.time"
								:text="item.text"
								:active="index === 0"
							>
							</timeline-item>
						</timeline>
					</scroll-view>
				</view>

				<!-- 操作 -->
				<view class="track-actions">
					<view class="track-action-item upload-order">
						<text>上传回单</text>
					</view>
					<view class="track-action-item upload-book">
						<text>上传委托书</text>
					</view>
				</view>
				
			</view>
			
		</template>
	</view>
</template>

<script>
	import OrderDeliveryInfo from '@/components/order-parts/order-delivery-info/index.vue'
	import OrderHeader from '@/components/order-item/components/order-header/index.vue'
	import Timeline from '@/components/timeline/index.vue'
	import TimelineItem from '@/components/timeline-item/index.vue'
	
	export default {
		name: 'DeliveryOrderDetail',
		components: {
			OrderHeader,
			OrderDeliveryInfo,
			Timeline,
			TimelineItem
		},
		data() {
			return {
				orderId: null,
				order: null,
				timelineList: [
					{
						time: '08-15 13:52',
						text: '[杭州市] 您的包裹已到达 [杭州分拨中心]'
					},
					{
						time: '08-15 13:52',
						text: '[杭州市] 您的包裹已到达 [杭州分拨中心]'
					},
					{
						time: '08-15 13:52',
						text: '[杭州市] 您的包裹已到达 [杭州分拨中心]'
					},
					{
						time: '08-15 13:52',
						text: '[杭州市] 您的包裹已到达 [杭州分拨中心]'
					}
				]
			}
		},
		onLoad(option) {
			this.getOrder(option.orderId)
		},
		methods: {
			getOrder(orderId) {
				if (!orderId) {
					return false
				}
				
				this.orderId = orderId
				
				this.order = {
					lng: 116,
					lat: 30,
					code: 'xxxxxxxxxxxx',
					startCity: '宁波市',
					startName: 'xxx',
					endCity: '宁波市',
					endName: 'xxx',
					deliveryTime: '2020-12-01 12:00:00',
					arriveTime: '2020-12-01 12:00:00'
				}
			},
			onClickFiles() {
				uni.navigateTo({
					url: `/pages/delivery-order-files/index?orderId=${this.orderId}`
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.delivery-order-detail {
		position: relative;
		height: 100%;
	}

	.track-map {
		height: 50%;
		map {
			width: 100%;
			height: 100%;
		}
	}

	.track-text-box {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: calc(60% + 30upx);
		padding: 0 30upx;
		border-radius: 30upx 30upx 0px 0px;
		box-shadow: 0px -4upx 20upx 0px rgba(0,0,0,0.1000);
		z-index: 1;
		background-color: #fff;
		
		@include flex-direction;
	}

	.order-header-out {
		flex-shrink: 0;
	}

	.stub-button {
		line-height: 52upx;
		height: 56upx;
		color: $theme-color;
		border: 2upx solid;
		padding: 0 28upx;
		border-radius: 28upx;
		box-sizing: border-box;
	}
	
	.order-delivery-info-out {
		flex-shrink: 0;
	}
	
	.track-list {
		border-top: 2upx solid $border-color;
		padding-top: 30upx;
		flex-grow: 1;
		height: 0;
		
		scroll-view {
			height: 100%;
		}
	}
	
	.track-actions {
		position: relative;
		flex-shrink: 0;
		padding: 30upx;
		box-shadow: 0px -4upx 20upx 0px rgba(0,0,0,0.0500);
		@include flex-center;
		margin-left: -30upx;
		margin-right: -30upx;
		z-index: 1;
		background-color: #fff;
	}
	
	.track-action-item {
		line-height: 88upx;
		text-align: center;
		border-radius: 44upx;
		color: #fff;
		flex-grow: 1;
		width: 0;
		
		&.upload-order {
			background-color: $sub-color;
		}
		
		&.upload-book {
			background-color: $theme-color;
		}
		
		& + .track-action-item {
			margin-left: 30upx;
		}
	}
</style>
